.droppableRow {
    min-height: 100px;
    min-width: 300px;
    display: flex;
    flex-direction: row;
    background-color: lightgray;
} 
.tile {
    width: 100px;
    height: 100px;
}

.tile:hover {
    cursor: pointer;
}

#blue.tile {
    background: #0065B2;
}

#red.tile {
    background: #A03000;
}
.page {
    display: flex;
    list-style-type: none;
}
.droppableContainer {
    width: 100px;
    height: 100px;
    background: #F5EEEC;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.grey {
    background: #E5DEDC;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  height: 100%;
  overflow: auto;
}

html {
  overflow: hidden;
  height: 100%;
}
